//iconscout_unicons
.uil {
    color: #071330;
    font-size: 30px;
    display: block;
    text-align: center;

    &.small-icons{
        font-size: 22px;
        width: 22px;
        &::before{
            line-height: 0;
        }
    }

    &.sidebar-icn{
        color: $white;
        font-size: 20px;
    }

    &.med-icons{
        font-size: 24px;
    }

    &.tiny-icons{
        font-size: 18px;
    }

    &.uil-facebook-f{
        font-size: 22px;
        color: #1976D2;
        @include respond-max(lg-tablet) {
            font-size: 20px;
        }
    }
}

.bg-green-icons{
    background: #D3EADD;
    border-radius: 50px;
    padding: 3px 10px;
    i{
        color: #219653;
        font-size: 12px;
    }
}

.action-icons{
    padding: 5px 8px;
    border-radius: 15px;
    margin: 5px;
    display: inline-flex;
    i{
        font-size: 18px;
        color: $white;
    }
    &.--archived-icn{
        background-color: #9B51E0;
    }
    &.--deleted-icn{
        background-color: #EB5757;
    }
    &.--inactive-icn{
        background-color: #F2C94C;
    }
}

.icn-slider-wft{
    background-image: url('../../../assets/icons/slider.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 16px;
    width: 16px;
    display: inline-block;
}

.icn-google-wtf{
    background-image: url('../../../assets/icons/google.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 20px;
    width: 20px;
    @include respond-max(lg-tablet) {
        height: 18px;
        width: 18px;
    }
}