// -----------------------------------------------------------------------------
// This file contains all forms style.
// -----------------------------------------------------------------------------

.ct-form-wtf{
    .form-group{
        margin-bottom: 20px;

        &:last-child{
            margin-bottom: 0;
        }
        .input-inline-wtf{
            text-align: left;
        }
        .float-inpt-fld{
            position: relative;
            .field-icon {
                position: absolute;
                right: 20px;
                top: 15px;
                font-size: 16px;
                color: rgba(0, 0, 0, 0.54);
            }
        }
        .list-sub-check{
            margin-left: 20px;
        }
        .input-group {
            &.--readonly{
                background-color: #eee;
            }
            &.--grp-datcpck{
                border-radius: 5px !important;
                border: 1px solid rgba(0, 0, 0, 0.2);
                transition: all .3s ease-in-out;
                @include on-event{
                    input{
                        background-color: transparent;
                    }
                }
                input{
                    width: 100%;
                    margin-top: 4px;
                    @include on-event{
                        background-color: transparent;
                    }
                }
                input, .input-group-addon{
                    border: none;
                }
                .input-group-addon{
                    background-color: transparent;
                    padding: 0;

                    i{
                        font-size: 18px;
                    }
                }
            }
        }
        .control-label{
            color: $black;
            text-transform: capitalize;
            text-align: left;
            margin-bottom: 10px;
            font-family: "poppins_med", sans-serif !important;
        }
        .form-control{
            color: rgba(0, 0, 0, 0.8);
            text-align: left;
            box-shadow: none;
            padding: 10px 20px;
            background: $white;
            margin-bottom: 10px;
            width: 100%;
            height: auto;
            border: 1px solid rgba(0, 0, 0, 0.2);
            border-radius: 4px;

            &::placeholder{
                color: rgba(0, 0, 0, 0.4);
            }
            @include on-event{
                background: rgba(47, 128, 237, 0.05); 
            }
        }
        .help-block{
            position: relative;
            color: #EB5757;
            font-size: 13px;
            padding-left: 30px;

            &::before{
                position: absolute;
                content: '';
                top: 0;
                left: 0;
                background-image: url('../../../assets/icons/warning.png');
                background-position: center;
                background-repeat: no-repeat;
                background-size: contain;
                height: 22px;
                width: 22px;
                border-radius: 100%;
            }
        }
        .select-custom-wft{
            position: relative;
            select[multiple="multiple"]{
                height: 100% !important;
            }
            .select2-search{
                min-height: 37px;
            }
            &.select2custom{
                .select2-selection{
                    padding: 4px 20px !important;
                    
                    @include on-event{
                        background: rgba(47, 128, 237, 0.05);
                    }
                }
                .select2-container{
                    .select2-selection__arrow{
                        b{
                            position: relative;
                            border: unset;
    
                            &::before{
                                font-family: FontAwesome;
                                content: '\f0d7';
                                font-size: 20px;
                                position: absolute;
                                top: -6px;
                                left: -11px;
                                color: rgba(0, 0, 0, 0.8);
                                pointer-events: none;
                                z-index: 2;
                            }
                        }
                    }
                    
                    &.select2-container--open{
                        .select2-selection__arrow{
                            b{
                                &::before{
                                    content: '\f0d8';
                                }
                            }
                        }
                    }
                }
                .select2-selection, .select2-search{
                    min-height: 44px !important;
                    height: auto !important;
                }
    
                .select2-selection__rendered, .select2-search{
                    padding-left: 0 !important;
                }
    
                .select2-selection__rendered{
                    line-height: 32px !important;
                }
    
                .select2-selection--multiple{
                    .select2-selection__rendered{
                        line-height: 22px !important;
                    }
                }
    
                .select2-selection__choice{
                    &:first-child{
                        margin-left: 0 !important;
                    }
                }

                &::after{
                    display: none;
                }
            }

            &::after{
                font-family: FontAwesome;
                content: '\f0d7';
                font-size: 20px;
                position: absolute;
                top: 10px;
                right: 15px;
                color: rgba(0, 0, 0, 0.8);
                pointer-events: none;
                z-index: 2;
            }
            select{
                appearance: none;
                padding-right: 35px;
    
                option{
                    &:disabled{
                        color: rgba(0, 0, 0, 0.4);
                    }
                }
    
                &::-ms-expand{
                    display: none;
                }

                &.select2{
                    width: 100% !important;
                    .selection{
                        .select2-selection{
                            border: unset;
                            outline: unset;
                            border-radius: 0;
                            height: auto;
                            padding: 0;

                            .select2-selection__rendered{
                                padding: 0;
                            }

                            .select2-selection__arrow{
                                height: 0;
                                width: 0;
                                b{
                                    position: relative;
                                    border: unset;
                                    left: unset;
                                    margin: 0;

                                    &:after{
                                        font-family: "Font Awesome 5 Free" !important;
                                        position: absolute;
                                        pointer-events: none;
                                        content: "\f107";
                                        color: #E24667;
                                        right: 13px;

                                        top: 6px;
                                        font-size: 20px;
                                        transform: translateX(-50%);

                                        @-moz-document url-prefix() {
                                            top: 25px;
                                            font-size: 20px;
                                        }
                                    }
                                }
                            }

                            &[aria-expanded="true"]{
                                .select2-selection__arrow{
                                    b{
                                        &:after{
                                            content: "\f106";
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        input{
            &[type="radio"]{
                &:checked{
                    + label{
                        &::after{
                            opacity: 1;
                            transform: scale(1);
                        }

                        &::before{
                            border-color: transparent;
                            background: #00C4D3;
                        }
                    }
                }
                &:not(:checked) {
                    + label {
                        &::before{
                            background-color: $white;
                            border: 1px solid rgba(0, 0, 0, 0.1);
                        }
                        &::after{
                            opacity: 0;
                            transform: scale(0);
                        }
                    }
                }
                &:checked, &:not(:checked){
                    position: absolute;
                    padding-left: 30px;
                    + label {
                        position: relative;
                        padding-left: 35px;
                        cursor: pointer;
                        line-height: 22px;
                        display: inline-block;

                        &::before, &::after{
                            content: '';
                            position: absolute;
                            border-radius: 100%;
                            transition: all 0.2s ease;
                        }

                        &::before{
                            left: 0;
                            top: 0;
                            width: 20px;
                            height: 20px;
                        }

                        &::after{
                            width: 8px;
                            height: 8px;
                            background: $white;
                            top: 6px;
                            left: 6px;
                        }
                    }
                }
            }
            &[type="checkbox"]{
                &:checked{
                    + label{
                        &::before{
                            border-color: transparent;
                            background: #00C4D3;
                        }
                    }
                }
                &:not(:checked) {
                    + label {
                        &::before{
                            border: 1px solid $black;
                        }
                    }
                }
                &:checked, &:not(:checked){
                    + label {
                        padding-left: 15px;
                        cursor: pointer;

                        &::before, &::after{
                            height: 20px;
                            width: 20px;
                            border-radius: 3.5px;
                            transition: all 0.2s ease;
                        }
                        &::after{
                            color: $white;
                            font-size: 10px;
                            padding: 0;
                            line-height: 21.5px;
                            padding-left: 4.5px;
                        }
                        &::before{
                            margin-top: 1px;
                        }
                    }
                }
            }
        }
        .chng-img-usr {
            position: relative;
            width: 82px;
            height: 82px;
            margin: auto;
            .avatar-upload {
                .avatar-edit {
                    position: relative;
                    z-index: 1;

                    input {
                        display: none;
                    }
                }

                .avatar-preview {
                    width: 82px;
                    height: 82px;
                    margin: auto;
                    position: relative;

                    #imagePreview {
                        width: 82px;
                        height: 82px;
                        background-size: cover;
                        background-repeat: no-repeat;
                        background-position: center;
                        border-radius: 100%;
                    }
                }
            }

            #changeimgs{
                position: absolute;
                bottom: 0;
                right: 0;
                cursor: pointer;
                display: block;
                background: $white;
                box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15);
                width: 26px;
                height: 26px;
                border-radius: 100%;
                margin-bottom: 0;
                i{
                    font-size: 16px;
                    line-height: 25px;
                }
            }
        }
        textarea{
            .form-control{
                height: 150px !important;
            }
        }
    }
}

.datepicker{
    th, td{
        width: auto;
    }
    &.datepicker-dropdown{
        margin-top: 10px !important;
    }
}