///start : main brand color

//main colors
$black: rgba(0, 0, 0, 0.8);
$white: #ffffff;

//main background
$main-bg: #EDF1F5;;
$blue-bg: #0B7C96;
$light-blue: #00AEEF;
$light-grey: #FAFAFA;
$pink-bg: #F33155;
$bg-light-blue: #F7FAFC;
$bg-yellow: #FDB13D;
$bg-green: #1CA345;
$bg-purple: #9B51E0;
$btn-light: rgba(0, 0, 0, 0.03);
$dark: #4C5667;
$primary-light: #DEDEDE;

//text color
$grey: #54667A;
$dark-gry: #2F323E;
$light-txt: rgba(0, 0, 0, 0.4);
$drk-lght: rgba(0, 0, 0, 0.6);


// Breakpoints map
/// @prop {String} keys - Keys are identifiers mapped to a given length
/// @prop {Map} values - Values are actual breakpoints expressed in pixels
$breakpoints: (
    'lg-laptop': 1400px,
    'lg-dekstop': 1199px,
    'mid-md-dekstop': 992px,
    'md-dekstop': 991px,
    'mid-lg-tablet': 768px,
    'lg-tablet': 767px,
    'mid-sm-tablet': 576px,
    'sm-tablet': 575px,
    'mid-lg-mobile': 425px,
    'lg-mobile': 424px
) !default;
