// -----------------------------------------------------------------------------
// This file contains all button style.
// -----------------------------------------------------------------------------

.btn-wtf{
    padding: 0;
    display: block;
    border: none;
    border-radius: 5px;
    color: $white;
    margin: 0;
    text-transform: capitalize;
    border: 1px solid transparent;
    max-width: 100% !important;
    font-family: "poppins_med", sans-serif !important;
    cursor: pointer;

    &.brd-rad-transparent-btn{
        padding: 5px 15px;
        background-color: transparent;
        border: 1px solid rgba(7, 19, 48, 0.2);
        border-radius: 50px;
        padding-right: 10px;
    }

    &.btn-flexs{
        display: inline-flex;
        align-items: center;
        gap: 10px;
    }
    
    &.addbtn{
        padding: 10px 15px;
        background-color: #2F80ED;
        border-radius: 8px;

        @include on-event{
            color: $white !important;
        }
    }

    &.btn-copy-wtf{
        background-color: transparent;
        display: inline-block;
        margin-left: 10px;
        i{
            color: #2F80ED;
            font-size: 20px;
        }
    }

    &.filterbtn{
        border: 1px solid #071330;
        background-color: transparent;
        color: #071330;
        padding: 10px 20px;
        border-radius: 50px;
        align-items: center;
    }

    &.--dark{
        border: 1px solid #071330;
        background-color: #071330;
        color: $white;
        padding: 10px 20px;
        border-radius: 6px;
    }

    &.--light{
        border: 1px solid #071330;
        background-color: transparent;
        color: #071330;
        padding: 10px 20px;
        border-radius: 6px;
    }

    &.--green{
        border: 1px solid #219653;
        background-color: #219653;
        color: $white;
        padding: 10px 20px;
        border-radius: 6px;
    }

    &.gap-btn{
        margin: 5px !important;
        
        &:first-child{
            margin-left: 0 !important;
        }

        &:last-child{
            margin-right: 0 !important;
        }
    }
}

.btn-slect-drp{
    width: 100%;
    text-align: left;
    padding: 9px 15px;
    position: relative;
    border-radius: 5px !important;
    border: 1px solid rgba(0, 0, 0, 0.2);
    transition: all .3s ease-in-out;
    background-color: $white;
    box-shadow: unset !important;
    text-transform: capitalize;

    &:after{
        position: absolute;
        right: 15px;
        font: normal normal normal 14px/1 FontAwesome;
    }

    &[aria-expanded="true"]{
        border-color: #7460EF;
        &:after{
            content: '\f0de';
            top: 15px;
            color: #7460EF;
        }
    }

    &[aria-expanded="false"]{
        &:after{
            content: '\f0dd';
            top: 10px;
        }
    }
}